import { makeStyles } from "@mui/styles";

export const useStyles = makeStyles((ctx) => ({
  header: {
    padding: "5px",
  },
  topHeader: {
    display: "flex",
    alignItems: "center",
    padding: "10px",
    border: `1px solid ${ctx.palette.primary.main}`,
    borderTopLeftRadius: "25px",
    position: "relative",
  },
  bottomHeader: {
    display: "flex",
    maxWidth: `${ctx.palette.primary.mainMaxWidht}`,
    margin: "0 auto",
    "@media(max-width: 750px)": {
      flexDirection: "column-reverse",
    },
  },
  headerInfo: {
    width: "50%",
    paddingLeft: "20px",
  },
  logo: {
    height: "150px",
    "@media(max-width: 1140px)": {
      height: "100px",
    },
  },
}));
