import { makeStyles } from "@mui/styles";

export const useStyles = makeStyles((ctx) => ({
  footer: {
    "@media(max-width: 760px)": {
      flexDirection: "column-reverse",
      alignItems: "center",
    },
  },
  footerInfo: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    width: "30%",
    marginTop: "50px",
    textAlign: "center",
  },
  footerInfoList: {
    padding: "0",
  },
  footerInfoItem: {
    listStyleType: "none",
    marginBottom: "15px",
    cursor: "pointer",
  },
  footerInfoLink: {
    textDecoration: "none",
    color: "black",
    "&&:hover": {
      color: `${ctx.palette.primary.main}`,
    },
  },
}));
