import { makeStyles } from "@mui/styles";

export const useStyles = makeStyles((ctx) => ({
  mobileMenuContainer: {
    position: "absolute",
    transitionDuration: ".4s",
    zIndex: "1000",
    background: `${ctx.palette.primary.mobileMenyBackgraund}`,
    boxShadow: "6px 4px 35px rgba(0, 0, 0, 0.21)",
    right: "0",
    top: "5px",
  },
  mobileMenuList: {
    listStyle: "none",
    padding: "5px",
    boxSizing: "border-box",
    position: "relative",
    marginTop: "40px",
  },
  mobileMenuItem: {
    marginBottom: "20px",
    borderBottom: `1px double ${ctx.palette.primary.main}`,
    padding: "10px",
  },
  closeIcon: {
    position: "absolute",
    top: "-25px",
    right: "5px",
  },
  mobileMenuLink: {
    color: "black",
    textDecoration: "none",
  },
}));
