import { makeStyles } from "@mui/styles";

export const useStyles = makeStyles((ctx) => ({
  socialMedia: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    width: "30%",
    "@media(max-width: 760px)": {
      width: "300px",
    },
  },
  footerLogo: {
    height: "150px",
    "@media(max-width: 760px)": {
      height: "100px",
    },
  },
  copyRight: {
    fontSize: "9px",
    lineHeight: "12px",
    width: "100%",
    textAlign: "center",
  },
}));
