import AwesomeSlider from "react-awesome-slider";
import "react-awesome-slider/dist/styles.css";
import { useStyles } from "./SliderContainerStyles";
import withAutoplay from "react-awesome-slider/dist/autoplay";
import slider1 from "../../../img/slider/slider1.jpg";
import slider2 from "../../../img/slider/slider2.jpg";
import slider3 from "../../../img/slider/slider3.jpeg";

export function SliderContainer() {
  const styles = useStyles();
  const AutoplaySlider = withAutoplay(AwesomeSlider);
  return (
    <AutoplaySlider
      organicArrows={false}
      play
      bullets={false}
      interval={2000}
      className={styles.slider}
    >
      <div data-src={slider1} />
      <div data-src={slider2} />
      <div data-src={slider3} />
    </AutoplaySlider>
  );
}
