import "./HeaderMenuStyles.js";
import MenuTwoToneIcon from "@mui/icons-material/MenuTwoTone";
import { useStyles } from "./HeaderMenuStyles";
import { MobileMenu } from "../MobileMenu";
import { useState } from "react";
import { Link } from "react-router-dom";

export function HeaderMenu() {
  const [mobileMenuVisible, setMobileMenuVisible] = useState(false);

  const clickHandlerMenuButton = () => {
    setMobileMenuVisible(!mobileMenuVisible);
  };

  const styles = useStyles();

  return (
    <div className={styles.mainMenu}>
      <nav className={styles.menu}>
        <ul className={styles.menuList}>
          <li className={styles.menuItem}>
            <Link className={styles.menuLink} to="/proecting">
              Проектирование тепловых пунктов и ЦТП
            </Link>
          </li>
          <li className={styles.menuItem}>
            <Link className={styles.menuLink} to="/montage">
              Монтаж тепловых пунктов и ЦТП
            </Link>
          </li>
          <li className={styles.menuItem}>
            <Link className={styles.menuLink} to="/delivery">
              Сдача тепловых пунктов в МОЭК и ТСК Мосэнерго
            </Link>
          </li>
          <li className={styles.menuItem}>
            <Link className={styles.menuLink} to="/contacts">
              Контакты
            </Link>
          </li>
        </ul>
      </nav>
      <MenuTwoToneIcon
        onClick={clickHandlerMenuButton}
        fontSize="large"
        className={styles.menuButton}
      />
      {mobileMenuVisible && (
        <MobileMenu clickHandlerMenuButton={clickHandlerMenuButton} />
      )}
    </div>
  );
}
