import { makeStyles } from "@mui/styles";

export const useStyles = makeStyles((ctx) => ({
  slider: {
    marginTop: "15px",
    height: "40vh",
    width: "50% !important",
    "@media (max-width: 750px)": {
      marginTop: "15px",
      height: "40vh",
      width: "100% !important",
    },
  },
}));
