import { makeStyles } from "@mui/styles";

export const useStyles = makeStyles((ctx) => ({
  headerInfo: {
    width: "50%",
    paddingLeft: "20px",
    "@media(max-width: 750px)": {
      padding: "0",
      width: "100%",
    },
  },
  portfolioList: {
    width: "auto",
    "@media(max-width: 750px)": {
      display: "none",
    },
  },
  portfolioListHeader: {
    width: "auto",
    "@media(max-width: 750px)": {
      display: "none",
    },
  },
  mainHeader: {
    width: "auto",
    "@media(max-width: 750px)": {
      textAlign: "center",
      fontSize: "larger",
    },
  },
}));
