import { makeStyles } from "@mui/styles";

export const useStyles = makeStyles((ctx) => ({
  footerMenu: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    width: "30%",
    "@media(max-width: 760px)": {
      width: "100%",
    },
  },
  footerMenuList: {
    padding: "0",
    marginTop: "80px",
    "@media(max-width: 760px)": {
      padding: "0",
      marginTop: "15px",
    },
  },
  footerMenuItem: {
    listStyleType: "none",
    marginBottom: "15px",
    cursor: "pointer",
    textAlign: "center",
  },
  footerMenuLink: {
    textDecoration: "none",
    color: "black",
    "&&:hover": {
      color: `${ctx.palette.primary.main}`,
    },
  },
}));
